html {
  font-size: 100%;
  box-sizing: border-box;
}

*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  min-height: 100vh;
  font-family: "Inter", sans-serif;
  line-height: 1.7;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin-top: 0;
}

h1,
h2,
h3 {
  line-height: 1.1;
}

:root {
  --c-white: hsl(36, 100%, 99%);
  --c-blue: hsl(240, 100%, 5%);
  --c-gray: hsl(236, 13%, 42%);
  --c-red: hsl(5, 85%, 63%);
  --c-silver: hsl(233, 8%, 79%);
  --c-yellow: hsl(35, 77%, 62%);
  --f-xl: clamp(
  	u.rem(40),
  	2.147887323943662rem + 1.5023474178403755vw,
  	u.rem(56)
  );
  --f-l: clamp(
  	u.rem(32),
  	1.823943661971831rem + 0.7511737089201878vw,
  	u.rem(40)
  );
  --f-m: u.rem(32);
  --f-s: u.rem(20);
  --f-xs: u.rem(18);
  --f-body: u.rem(15);
}

.wrapper {
  width: min(69.375rem, 100% - 2rem);
  margin-inline: auto;
}